.component-container{
  display:flex; 
  flex-direction:column;
  justify-content:center;
  align-items:center;
}

.bar-chart-container{
    margin-top: 10px;
    margin-left: 10px;
    /* width:1200px; */
    width: 700px;
    height:900px;
    margin-bottom:20px;
    overflow: auto;
}

.legend-section{
  overflow:auto;
  width:calc(65% - 1px);
}

.legend-container{
  height:135px; 
  width:800px; 
  display:flex; 
  justify-content:center;
  align-items:center;
  flex-wrap:wrap;
  column-gap:10px;
  row-gap: 5px;
}

.legend-items{
  flex-basis:28%;
  white-space:nowrap;
  width:70px;
}



